import { Link } from "react-scroll"
export default function Carousel() {
    return (
        <div>
            <header>
                <div className="w-full bg-center bg-cover h-128">
                    <div className="flex items-end justify-center w-full h-full text-white bg-gray-700 md:items-center">
                        Slider (Por realizar Viernes)
                        <div className="hidden py-8 text-center">
                            <h1 className="px-4 mb-6 text-2xl font-semibold text-white uppercase lg:text-3xl">hola<span className="text-crismon-200">mundo</span></h1>
                            <Link to="pricing" smooth={true} duration={1000} className="font-medium text-white uppercase btn bg-maroon-700 hover:bg-maroon-800 lg:hidden">Hola</Link>
                        </div>
                    </div>
                </div>
            </header>
        </div>
    )
}
