import React from "react";
import { Link } from "react-router-dom";

import WebHome from "../../Files/DisenoWeb/WebHome.jpg";
import Marketing from '../../Files/DisenoWeb/Marketing-Digital.pdf'
import WebVentajas from "../../Files/DisenoWeb/WebVentajas.jpg";
import WebMarketing from "../../Files/DisenoWeb/WebMarketing.jpg";
import WebDigitaliza from "../../Files/DisenoWeb/WebDigitaliza.jpg";
import Safari from "../../Files/DisenoWeb/Safari.png";
import Macbook from "../../Files/DisenoWeb/Macbook.png";

import HeaderSolutions from "../../Components/HeaderSolutions/HeaderSolutions";
import { LightningBoltIcon } from '@heroicons/react/outline'

export default function pbxCloud() {
  return (
    <div className="bg-gray-100 dark:bg-dark-800">
      {/*Header */}
      <HeaderSolutions
        img={WebHome}
        title="Mejora la eficiencia y procesos de tu negocio o emprendimiento, a través de"
        subtitle="aplicaciones Web o de Escritorio"
        button="ver e-book"
      />


      {/*Hero */}
      <div className="p-6 md:p-8">
        <section className="px-4 py-24 mx-auto max-w-7xl">
          <div className="w-full mx-auto text-left md:w-11/12 xl:w-9/12 md:text-center">
            <h1 className="mb-6 font-sans text-4xl font-extrabold leading-none tracking-normal text-gray-900 md:text-6xl md:tracking-tight">
              Generamos{" "}
              <span className="block w-full pb-2 text-transparent bg-clip-text bg-gradient-to-r from-indigo-500 to-maroon-600 lg:inline">
                su imagen
              </span>{" "}
              frente al mundo.
            </h1>
            <p className="px-0 mb-6 text-lg text-gray-600 md:text-xl lg:px-24">
              Desarrollamos e Implementamos Páginas y Sitios Web, plataformas de
              comercialización de contenido, Diseño Web, SEO, WHCMS, Tiendas
              virtuales y mucho más.
            </p>
            <div className="mb-4 space-x-0 md:space-x-2 md:mb-8">
              <Link
                className="text-white btn bg-maroon-700 hover:bg-crismon-500"
                to="/contacto"
              >
                ¿Te interesa?
              </Link>
            </div>
          </div>
          <div className="w-full mx-auto mt-20 text-center md:w-10/12">
            <img
              src={Safari}
              alt="Diseños Web con Schnelke"
              className="w-full rounded-lg shadow-2xl"
            />
          </div>
        </section>
      </div>

      {/*Aplicaciones de Escritorio */}
      <div className="relative flex flex-col-reverse px-4 py-16 mx-auto lg:block lg:flex-col lg:py-32 xl:py-48 md:px-8 sm:max-w-xl md:max-w-full">
          <div className="z-0 flex justify-center h-full -mx-4 overflow-hidden lg:pt-24 lg:pb-16 lg:pr-8 xl:pr-0 lg:w-1/2 lg:absolute lg:justify-end lg:bottom-0 lg:left-0 lg:items-center">
            <img
              src={Macbook}
              className="object-cover object-right w-full h-auto lg:w-auto lg:h-full"
              alt="Aplicaciones de Escritorio"
            />
          </div>
          <div className="relative flex justify-end max-w-xl mx-auto xl:pr-32 lg:max-w-screen-xl">
            <div className="mb-16 lg:pr-5 lg:max-w-lg lg:mb-0">
              <div className="max-w-xl mb-6">
                <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl sm:leading-none">
                  Desarrollo e implementación
                  <span className="inline-block text-deep-purple-accent-400">
                    de Aplicaciones de Escritorio multipropósito
                  </span>
                </h2>
                <p className="text-base text-gray-700 md:text-lg">
                  Toma Desiciones Más Acertadas basadas en la Información
                  proveniente de Tus Aplicaciones de Control y Producción,
                  desarrollamos CRM, Logística y rastreo Inventarios y
                  Administración hasta tableros de producción.
                </p>
              </div>
            </div>
          </div>
        </div>

        <div className="px-4 py-16 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20">
          <div className="grid gap-5 row-gap-10 lg:grid-cols-2">
            <div className="flex flex-col justify-center">
              <div className="max-w-xl mb-6">
                <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl sm:leading-none">
                  Conoce
                  <br className="hidden md:block" />
                  nuestras{' '}
                  <span className="relative px-1">
                    <div className="absolute inset-x-0 bottom-0 h-3 transform -skew-x-12 bg-oxford-500" />
                    <span className="relative inline-block text-maroon-700">
                      Ventajas
                    </span>
                  </span>
                </h2>
              </div>
              <div className="grid space-y-3 sm:gap-2 sm:grid-cols-2 sm:space-y-0">
                <ul className="space-y-3">
                  <li className="flex pt-2" >
                    <span className="mr-1">
                      <LightningBoltIcon className="w-5 h-5 text-maroon-700" />
                    </span>
                    Administración de tu proyecto 
                  </li>
                  <li className="flex pt-2">
                    <span className="mr-1">
                    <LightningBoltIcon className="w-5 h-5 text-maroon-700" />
                    </span>
                    Desarrollo personalizado 
                  </li>
                  <li className="flex pt-2">
                    <span className="mr-1">
                    <LightningBoltIcon className="w-5 h-5 text-maroon-700" />
                    </span>
                    Soporte de ciclo de vida 
                  </li>
                  <li className="flex pt-2">
                    <span className="mr-1">
                    <LightningBoltIcon className="w-5 h-5 text-maroon-700" />
                    </span>
                    Desarrollo personalizado 
                  </li>
                  <li className="flex pt-2">
                    <span className="mr-1">
                    <LightningBoltIcon className="w-5 h-5 text-maroon-700" />
                    </span>
                    Desarrollos para emprendimientos y PYMEs 
                  </li>
                </ul>
              </div>
            </div>
            <div>
              <img
                className="object-cover w-full h-56 rounded shadow-lg sm:h-96"
                src={WebVentajas}
                alt="Ventjas Web con Schnelke"
              />
            </div>
          </div>
        </div>

        {/*Marketing Digital */}
        <div className="px-8">
          <div className="flex flex-col px-4 py-16 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20 lg:flex-row">
            <div className="mb-5 lg:mb-0 lg:mr-20">
              <h2 className="relative mb-4 font-sans text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl sm:leading-none">
                <span className="relative inline-block">
                  <svg
                    viewBox="0 0 52 24"
                    fill="currentColor"
                    className="absolute top-0 left-0 z-0 hidden w-32 -mt-8 -ml-20 text-blue-gray-100 lg:w-32 lg:-ml-28 lg:-mt-10 sm:block"
                  >
                    <defs>
                      <pattern
                        id="6bfa0e57-faa2-4bb2-ac0e-310782e5eb2d"
                        x="0"
                        y="0"
                        width=".135"
                        height=".30"
                      >
                        <circle cx="1" cy="1" r=".7" />
                      </pattern>
                    </defs>
                    <rect
                      fill="url(#6bfa0e57-faa2-4bb2-ac0e-310782e5eb2d)"
                      width="52"
                      height="24"
                    />
                  </svg>
                  <span className="relative">¿Por qué</span>
                </span>{" "}
                Marketing Digital?
              </h2>
              <p className="mb-4 text-gray-900 lg:mb-6">
                El Mundo de los negocios ha cambiado, y el impacto de la
                pandemia a causa del COVID-19 transformó esta era en una era
                digital, la forma de comprar y vender se ha transformado y es
                mucho más rápida y automática. Ya no se necesita de grandes
                inversiones para comenzar un negocio, pero si se requiere
                entender y conocer cómo utilizar las herramientas digitales a tu
                alcance para incrementar la velocidad y el alcance de la
                publicidad y el segmento de mercado al que llega dicha
                publicidad.
              </p>
            </div>
          </div>
          <div className="relative">
            <img
              className="object-cover w-full h-56 sm:h-96"
              src={WebMarketing}
              alt=""
            />
            <div className="absolute inset-0 bg-gray-900 bg-opacity-50" />
          </div>
        </div>

        {/*Digitalizate */}
        <section className="px-8 bg-gray-100 dark:bg-gray-900 lg:py-12 lg:flex lg:justify-center">
          <div className="w-full bg-white dark:bg-gray-800 lg:flex">
            <div className="lg:w-1/2">
              <div
                className="h-64 bg-cover lg:rounded-lg lg:h-full"
                style={{
                  backgroundImage:
                    `url(${WebDigitaliza})`,
                }}
              ></div>
            </div>

            <div className="max-w-xl px-6 py-12 lg:max-w-5xl lg:w-1/2">
              <h2 className="text-2xl font-bold text-gray-800 dark:text-white md:text-3xl">
                Digitaliza tu{" "}
                <span className="text-maroon-700">
                  publicidad{" "}
                </span>
              </h2>
              <p className="mt-4 text-gray-600 dark:text-gray-400">
                e incrementa el alcance y la velocidad con que llegas a tu
                mercado, da a conocer tu marca, producto o servicio y enfócate
                en tus resultados. En Schnelke te ayudamos a crear e integrar
                una campaña de publicidad eficiente para llegar a tu público
                objetivo y obtener suficientes clientes potenciales y
                conversiones (venta concretada) para impulsar tu negocio al
                siguiente nivel.
              </p>
            </div>
          </div>
        </section>
        
        {/*E-book */}
        <div className="overflow-hidden bg-gray-900" id="pricing">
          <div className="px-4 py-16 mx-auto sm:max-w-xl md:max-w-full lg:max-w-screen-xl md:px-24 lg:px-8 lg:py-20">
            <div className="flex flex-col items-center justify-between xl:flex-row">
              <div className="w-full max-w-xl mb-12 xl:pr-16 xl:mb-0 xl:w-7/12">
                <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold tracking-tight text-white sm:text-4xl sm:leading-none">
                  Apóyate en la Tecnología
                </h2>
                <p className="max-w-xl mb-4 text-base text-gray-400 md:text-lg">
                  para Agilizar y Proyectar tu Empresa hacia donde Quieres
                  Llevarla ... Nosotros Te Ayudamos
                </p>
                <a
                  href={Marketing}
                  aria-label=""
                  className="text-white btn bg-maroon-700 hover:bg-maroon-600"
                >
                  Descargar
                </a>
              </div>
            </div>
          </div>
        </div>
        
        {/*CTA */}
        
        <div className="relative w-full h-full px-8">
          <div className="container relative grid gap-8 px-4 pt-16 mx-auto xl:px-0 sm:grid-cols-1 md:grid-cols-2 lg:grid-cols-2 xl:grid-cols-2 lg:pt-36">
            <div className="text-color">
              <h1 className="mb-6 font-sans text-4xl font-extrabold leading-none tracking-normal text-gray-900 md:text-6xl md:tracking-tight">
                Crea, contacta, vende
              </h1>
              <div className="pt-10 pb-6 text-xl xl:pt-20">
                <h2>El futuro de tu empresa digital está en tus manos.</h2>
              </div>
              <Link to="/contacto" className="text-white btn bg-dark-800 hover:bg-dark-600">
                Construir
              </Link>
            </div>
            <div>
              <img
                className="w-full lg:-ml-16 xl:-ml-42"
                src="https://cdn.tuk.dev/assets/templates/prodify/desktop.png"
                alt=""
              />
            </div>
          </div>
        </div>
      
    </div>
  );
}
